























import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

@Component
export default class ClrGC extends Vue {
  @Prop() private title!: string;
  @Prop() private stateDashboard!: any;
  @Prop() private intervalTime!: any;
  get responseConfig() {
    return {
      color: ['#E3BE2B', '#3f96e3'],
      tooltip: {
        trigger: 'axis',
        backgroundColor: 'rgb(50,50,50)',
        textStyle: {
          fontSize: 13,
        },
      },
      legend: {
        icon: 'circle',
        top: 0,
        left: 0,
        itemWidth: 12,
      },
      grid: {
        top: 55,
        left: 0,
        right: 18,
        bottom: 15,
        containLabel: true,
      },
      xAxis: {
        type: 'category',
        axisTick: {
          lineStyle: { color: '#c1c5ca41' },
          alignWithLabel: true,
        },
        splitLine: { show: false },
        axisLine: { lineStyle: { color: 'rgba(0,0,0,0)' } },
        axisLabel: { color: '#9da5b2', fontSize: '11' },
      },
      yAxis: {
        type: 'value',
        axisLine: { show: false },
        axisTick: { show: false },
        splitLine: { lineStyle: { color: '#c1c5ca41', type: 'dashed' } },
        axisLabel: { color: '#9da5b2', fontSize: '11' },
      },
      series: [
        {
          data: this.stateDashboard.instanceInfo.clrGCGen0.map(
            (i: any, index: number) => [this.intervalTime[index], i.value],
          ),
          name: this.stateDashboard.instanceInfo.clrGCGen0.length
            ? 'gen0'
            : null,
          type: 'line',
          symbol: 'none',
          lineStyle: {
            width: 1.5,
          },
        },
        {
          data: this.stateDashboard.instanceInfo.clrGCGen1.map(
            (i: any, index: number) => [this.intervalTime[index], i.value],
          ),
          name: this.stateDashboard.instanceInfo.clrGCGen1.length
            ? 'gen1'
            : null,
          type: 'line',
          symbol: 'none',
          lineStyle: {
            width: 1.5,
          },
        },
        {
          data: this.stateDashboard.instanceInfo.clrGCGen2.map(
            (i: any, index: number) => [this.intervalTime[index], i.value],
          ),
          name: this.stateDashboard.instanceInfo.clrGCGen2.length
            ? 'gen2'
            : null,
          type: 'line',
          symbol: 'none',
          lineStyle: {
            width: 1.5,
          },
        },
      ],
    };
  }
}
