




























import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';
import Num from './chart-num.vue';

interface NumBoxData {
  value: number;
}
@Component({components: {Num}})
export default class Response extends Vue {
  @Prop() private title!: string;
  @Prop() private type!: string;
  @Prop() private stateDashboard!: any;
  @Prop() private intervalTime!: any;
  get content() {
    const temp = this.stateDashboard[this.type].getThroughputTrend
    .map((i: NumBoxData) => i.value).filter((i: any) => i);
    const sum = temp.length ? temp
    .reduce((preValue: number, curValue: number, index: number, array: number[]) => preValue + curValue)
    / this.stateDashboard[this.type].getThroughputTrend.length : 0;
    return sum;
  }
  get avg() {
     return this.stateDashboard[this.type].getThroughputTrend ?
      [{
        data: this.stateDashboard[this.type].getThroughputTrend.map((i: any, index: number) => [
          this.intervalTime[index],
          i.value,
        ]),
        name: this.stateDashboard[this.type].getThroughputTrend.length ? 'Avg Response' : null,
        type: 'line',
        symbol: 'none',
        lineStyle: {
          width: 1.5,
        },
      }] : [];
  }
  get responseConfig() {
    return {
      color: [
        '#3f96e3',
        '#6be6c1',
        '#626c91',
        '#a0a7e6',
        '#96dee8',
      ],
      grid: {
        top: 35,
        left: 0,
        right: 28,
        bottom: 0,
        containLabel: true,
      },
      tooltip: {
        trigger: 'axis',
        backgroundColor: 'rgb(50,50,50)',
        textStyle: {
          fontSize: 13,
        },
      },
      xAxis: {
        type: 'category',
        axisTick: {
          lineStyle: { color: '#c1c5ca41' },
          alignWithLabel: true,
        },
        splitLine: { show: false },
        axisLine: { lineStyle: { color: 'rgba(0,0,0,0)' } },
        axisLabel: { color: '#9da5b2', fontSize: '11' },
      },
      yAxis: {
        type: 'value',
        axisLine: { show: false },
        axisTick: { show: false },
        splitLine: { lineStyle: { color: '#c1c5ca41', type: 'dashed' } },
        axisLabel: { color: '#9da5b2', fontSize: '11' },
      },
      series: [
        ...this.avg,
      ],
    };
  }
}
